<template>
  <div id="category">
    <h2>{{current_category}}</h2>
    <ul class="productlist">
      <li class="productlist__item"
      v-for="(product, index) in this.filter_products"
      :key="index">
        <a href="#" @click.prevent='go_page("front/onlinestore/"+current_category+"/"+product.id)'>
          <div class="productlist__item__photo">
            <img :src="product.img" alt="" class="product">
            <img src="~@/assets/icon/new_tag.png" alt="" class="isnew"
            v-if="product.isnew">
          </div>
          <div class="productlist__item__main">
            <p class="title">{{product.title}}</p>
            <p class="code">{{product.id}}</p>
            <hr>
            <p class="price">NT$ {{product.price}}</p>
          </div>
        </a>
      </li>
    </ul>
  </div>
</template>
<script>
import $ from 'jquery';

export default {
  watch: {
    $route(to) {
      this.current_category = to.params.category;
      this.initialize_filter_products();
      document.title = `線上購物 | ${this.current_category}`;
    },
  },
  props: ['products'],
  data() {
    return {
      current_category: 'all',
      filter_products: [
        // {
        //   img: '',
        //   is_new: '',
        //   title: '',
        //   id: '',
        //   price: '',
        //   category: [],
        // },
      ],
    };
  },
  methods: {
    initialize_filter_products() {
      const copyProductsArray = JSON.parse(JSON.stringify(this.products));

      let filterArray = [];
      if (this.current_category === 'all') {
        filterArray = copyProductsArray;
      } else {
        filterArray = copyProductsArray
          .filter((product) => product.category.includes(this.current_category));
      }

      filterArray = filterArray.map((product) => {
        const tempObj = {};
        tempObj.title = product.title;
        tempObj.id = product.id;
        tempObj.price = product.price;
        tempObj.is_new = product.is_new;
        tempObj.img = `${product.images[0]}`;
        tempObj.category = product.category;
        return tempObj;
      });
      this.filter_products = filterArray;
    },
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
  },
  created() {
    this.current_category = this.$route.params.category;
    this.initialize_filter_products();
  },
  mounted() {
    $('.productlist').removeClass('d-none');
    document.title = `線上購物 | ${this.current_category}`;
  },
};
</script>
